body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.body-light {
  background-color: transparent;
  color: black;
}

/* dark theme style */
.body-dark {
  background-color: rgb(0, 0, 0);
  color: white;
}

.body-dark nav li a,
.body-dark nav li button,
.body-dark footer li a,
.body-dark .receipe-content-container button,
.body-dark .user-page-container .user-page-control-button-group button,
.body-dark .user-page-container .user-page-content .form-container .form-button button {
  color: white !important;
}

/* navbar */
/* Base Navbar Styling */
nav {
  margin: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 2px solid orangered;
  padding-bottom: 10px;
}

/* Home Link */
.home-link img {
  max-width: 150px;
}
.hamburger{
  display: none;
}
/* Center Links (Default - Desktop) */
.center-link {
  display: flex;
}

.center-link ul {
  list-style: none;
  display: flex;
  gap: 15px;
  align-items: center;
}

nav li a {
  color: black;
  text-decoration: none;
  font-size: 16px;
}

nav li button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

nav li a:hover,
.home-link a:hover {
  opacity: 0.6;
}

/* Navigation Links (User, Cart, Theme Mode) */
.nav-link ul {
  list-style: none;
  display: flex;
  gap: 15px;
  align-items: center;
}

/* Chat Button (Fixed on Screen) */
.fix-chat-btn {
  z-index: 9999;
  position: fixed;
  right: 20px;
  bottom: 15px;
  background-color: rgb(0, 0, 0);
  border-radius: 10px;
}

.fix-chat-btn i {
  font-size: 25px;
  padding: 5px;
}
.fix-chat-btn a{
  text-decoration: none;
}
.body-dark .fix-chat-btn button{
  border: 1px solid rgba(255, 255, 0, 0.475);
  border-radius: 15px;
}
.fix-chat-btn button {
  width: 100%;
  padding: 5px;
  color: white;
  border: none;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

/* ========================= */
/* MOBILE RESPONSIVE STYLES */
/* ========================= */

@media (max-width: 768px) {
  /* Hide Center Links by Default */
  .center-link {
    z-index: 9999;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.9);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in-out;
  }

  /* Open Class to Show Menu */
  .center-link.open {
    transform: translateX(0);
  }

  .center-link ul {
    flex-direction: column;
    text-align: center;
    gap: 25px;
  }

  .center-link ul li a {
    color: white;
    font-size: 20px;
    text-decoration: none;
  }

  /* Hamburger Button (Visible on Mobile) */
  .hamburger {
    display: block;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    color: black;
  }

  /* Hide Center Links on Mobile */
  .nav-link ul {
    display: flex;
    align-items: center;
    gap: 10px;
  }
}

